.Gatekeeper_login {
    width: 100vw;
    height: 100vh;
    max-width: 400px;
    background: #F6FAFF;

    .header {
        margin-top: 100px;
        text-align: center;

        h1 {
            font-size: 60px;
            font-weight: 500;
            font-family: 'Redressed', cursive;
        }

        span {
            font-family: 'Roboto', sans-serif;
            font-weight: bold;
            font-size: 14px;
            color: #474747;
            letter-spacing: .5px;
            position: relative;
            left: 55px;
            top: -20px;
        }
    }

    form {
        width: 80%;
        margin: auto;
        color: #474747;
        font-weight: 600;
        font-family: 'Roboto', sans-serif;
        letter-spacing: .5px;
        margin-top: 50px;

        .form-group {
            margin-top: 25px;
            position: relative;

            label {
                margin-bottom: 5px;
            }

            input {
                line-height: 33px;
                border-color: #C6C6C6;
                font-size: 14px;
                letter-spacing: .7px;
                padding-left: 50px;
                padding-right: 50px;
                border-radius: 10px;

                &::placeholder {
                    color: #C6C6C6;
                }
            }

            .icon1 {
                position: absolute;
                bottom: 12px;
                left: 13px;
                color: #C6C6C6;
            }

            .icon2 {
                position: absolute;
                bottom: 11px;
                right: 20px;
                color: #C6C6C6;
                cursor: pointer;
            }

            .error {
                color: red;
                font-size: 10px;
                position: absolute;
                left: 0px;
                bottom: -16px;
                font-weight: 400;
            }
        }

        .btnLogin {
            width: 100%;
            text-align: center;
            margin-top: 30px;

            .bt {
                width: 100%;
                line-height: 30px;
                border-radius: 10px;
                text-transform: none;
                font-weight: 500;
                letter-spacing: 1px;
                font-size: 15px;
            }
        }
    }
}


@media (max-width:500px) {
    .Gatekeeper_login {
        background-color: white;
    }
}