*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body::-webkit-scrollbar {
  display: none;
}

.main{
  width: 100%;
  height: 100vh;
  display: grid;
  place-content: center;
  background-color: rgb(237, 237, 237);
}

@media (max-width:420px) {
  body{
    background-color: rgb(245, 248, 252);
  }
}