.gateHeader {
    width: 90%;
    top: 0px;
    position: fixed;
    max-width: 400px;
    z-index: 2;

    .innerHead {
        width: 100%;
        margin: auto;
        height: 50px;
        padding-left: 10px;
        position: relative;
        background-color: white;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);

        .menuBt {
            position: absolute;
            left: 10px;
            bottom: 8px;
            cursor: pointer;
        }

        .clearBt {
            text-transform: none;
            height: 30px;
            position: absolute;
            right: 10px;
            bottom: 10px;
        }

    }
}